<template>
  <div>
    type:
    <Button></Button>
    <Button type="primary"></Button>
    <Button type="danger"></Button>
    <br/>
    size:
    <Button size="small"></Button>
    <Button></Button>
    <Button size="large"></Button>

    <br/>
    round:
    <Button></Button>
    <Button round></Button>

    <br/>
    plain:
    <Button plain ></Button>
    <Button type="primary" plain></Button>
    <Button type="danger" plain></Button>

    <br/>
    
    disabld:
    <Button disabled></Button>
    <Button type="primary" disabled></Button>
    <Button type="danger" disabled></Button>
    <Button plain disabled></Button>
    <Button type="primary" plain disabled></Button>
    <Button type="danger" plain disabled></Button>
  </div>
</template>

<script>
import Button from "@/components/button";
export default {
  name: "page-button",
  components: {
    Button
  }
};
</script>

<style scoped>

</style>


