          <td>rowstype = "textrea" </td>
          <td>textrea</td>
          <td>[Number]</td>
          <td>2</td>          
        </tr>
         <tr>
          <td>disabled</td>
          <td></td>
          <td>[Boolean]</td>
          <td>false</td>          
        </tr>
         <tr>
          <td>clearable</td>
          <td></td>
          <td>[Boolean]</td>
          <td>false</td>          
        </tr>
         <tr>
          <td>clearable</td>
          <td></td>
          <td>[Boolean]</td>
          <td>false</td>          
        </tr>
         <tr>
          <td>prefix-icon</td>
