    <p>type: "text"  / input2:{{input2}}</p>
    <Input placeholder="" v-model="input2" type="textarea" :rows="4"/>
    <p>disabled: true</p>
    <Input placeholder="" disabled/>
    <p>clearable: true</p>
    <Input placeholder="" clearable/>
    <p>prefix-icon</p>    
    <Input placeholder="" prefix-icon="search"/>
    <p>suffix-icon</p>    
    <Input placeholder="" suffix-icon="date"/>
    <Input placeholder="" suffix-icon="time"/>
  </div>
</template>

<script>
import Input from "@/components/input";
export default {
  name: "page-input",
  components: {
    Input
  },
  data() {
    return {
      input1: "",
      input2: ""
    };
  },
  methods: {
    test() {}
  },
  mounted() {}
};
</script>


