<template>
  <div>
    <table>
      <thead>
        <td></td>
        <td></td>
      </thead>
      <tbody>
        <tr>
          <td>data</td>
          <td>
            
            <br/>
             <code>Radio</code>   [Boolean]
            <br/>
              <code>RadioGroup</code> [String, Number, Boolean] </td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>[Boolean]</td>
        </tr>
        <tr>
          <td>label</td>
          <td>[String, Number]value</td>
        </tr>
      </tbody>
    </table>
    <table>
      <thead>
        <td></td>
        <td></td>
      </thead>
      <tbody>
        <tr>
          <td>change</td>
          <td></td>
        </tr>
      </tbody>
    </table>
    <p>- </p>
    <p>single - {{radio1}}</p>
    <Radio v-model="radio1"></Radio>
    <p>single - {{radio2}}</p>
    <Radio v-model="radio2"></Radio>

    <br/>
    <br/>
    <br/>

    <p>- </p>
    <p>group1 - {{radio3}}</p>
    <radio-group v-model="radio3">
        <Radio label="A" disabled>A</Radio>
        <Radio label="B">B</Radio>
        <Radio label="C">C</Radio>
    </radio-group>
    <p>group2 - {{radio4}}</p>
    <radio-group v-model="radio4">
        <Radio label="1">1</Radio>
        <Radio label="2">2</Radio>
        <Radio label="3">3</Radio>
    </radio-group>
  </div>
</template>

<script>
import {Radio,RadioGroup} from "@/components/radio";

export default {
  name: "page-radio",
  components: {
    Radio,
    RadioGroup
  },
  data() {
    return {
      radio1: false,
      radio2: true,
      radio3: "B",
      radio4: "1"
    };
  }
};
</script>


