<template>
  <div>
    <h3> cropper</h3>
    <table cellspacing="0" cellpadding="0" >
      <thead>
        <td></td>
        <td></td>
        <td></td>
      </thead>
      <tbody>
        <tr>
          <td>cropper-size</td>
          <td></td>
          <td>300</td>
        </tr>
        <tr>
          <td>preview-size</td>
          <td></td>
          <td>150</td>
        </tr>
        <tr>
          <td>mask</td>
          <td> "light"/"dark"</td>
          <td>"light"</td>
        </tr>
        <tr>
          <td>shape</td>
          <td> "square"/"round"</td>
          <td>"round"</td>
        </tr>
        <tr>
          <td>default-tips</td>
          <td></td>
          <td>"JPGPNG"</td>
        </tr>
        <tr>
          <td>defaultImg</td>
          <td></td>
          <td>-</td>
        </tr>
        <tr>
          <td>upload-props</td>
          <td> :<br/>
              action:<br/>
              headers :  <br/>
              data :	<br/>
              name: 	
            </td>    
          <td>-</td>
        </tr>
      </tbody>
    </table>

    <table cellspacing="0" cellpadding="0" >
      <thead>
        <td></td>
        <td></td>
        <td></td>
      </thead>
      <tbody>
        <tr>
          <td>beforeReadImg</td>
          <td> false </td>
          <td>-</td>
        </tr>
        <tr>
          <td>confirm</td>
          <td> true  false true</td>
          <td>-</td>
        </tr>
        <tr>
        <tr>
          <td>onProgress</td>
          <td></td>
          <td>event</td>
        </tr>
        <tr>
          <td>onSuccess</td>
          <td></td>
          <td>response,url</td>
        </tr>
        <tr>
          <td>onError</td>
          <td></td>
          <td>err, response</td>
        </tr>
        <tr>
          <td>clear</td>
          <td></td>
          <td>-</td>
        </tr>
      </tbody>
    </table>
    
    <div>
      <Cropper :upload-props="uploadProps"
      default-img = "/static/demo-110.png"
      @comfirm = "comfirm" 
      @handleProgress="handleProgress"
      @onSuccess="onSuccess"
      @clear="clear"/>
    </div>

    <br/>
    <hr/>
    <br/>

    <div>
      <Cropper :upload-props="uploadProps"
      mask="dark" shape="square"
      @comfirm = "comfirm" 
      @handleProgress="handleProgress"
      @onSuccess="onSuccess"
      @clear="clear"/>
    </div>
    
  </div>
</template>

<script>
import Cropper from "@/components/cropper";
export default {
  name: "page-cropper",
  components: {
    Cropper
  },
  data () {
    return {
      uploadProps:{
        action:"admin/api/upload",
        data:{
          "bizKey": "news"
        }
      }
    }
  },
  methods: {
    beforeReadImg(file) {
      console.log(file)
      if(file.raw.size>1024*10){
        return true
      }else{
        console.log('1024*10kb')
        return false;
      }
    },
    handleProgress(e,file){
      console.log(e)
      console.log(file)
    },
    comfirm(){
      return confirm("");
    },
    onSuccess(res,url){
      console.log(res)
      console.log(url)
    },
    clear(){
      console.log('')
    }
  },
};
</script>


