<template>
  <div>
    <Alert :showIcon="false"></Alert>    
    <Alert></Alert>
    <Alert center></Alert>
    <Alert type="warning"></Alert>
    <Alert type="success"></Alert>  
    <Alert type="error"></Alert>   
    <Alert type="error" closeable></Alert>    
  </div>
</template>


<script>
import Alert from "@/components/alert"
  export default {
    name: 'page-alert',
    components:{
      Alert
    }
  }
</script>

<style scoped>

</style>


